.productList {
  display: flex;
  flex-direction: column;
  min-height: 400px;
  margin-top: 40px;
  padding: 0 60px;
  box-sizing: border-box;
  .flexRow {
    display: flex;
    width: 100%; }
  &-content {
    width: 100%;
    // margin-bottom: 100px
    &-main {
      display: flex;
      flex-wrap: wrap;
      width: 100%;
      min-width: 300px;
      padding-top: 20px; } }
  &-empty {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
    padding: 20px;
    box-sizing: border-box; } }

@media (max-width: 959.5px) {
  .productList {
    width: 100%;
    padding: 0;
    align-items: center; } }
